@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}
.login{width:100vw;height:100vh;background-color:#fff;display:flex;justify-content:center;align-items:center}.login form{width:50rem;height:100%;display:flex;flex-direction:column;justify-content:center;box-shadow:0px 0px 10px 2px rgba(0,0,0,.59);padding:0rem 2rem}.login form h1{font-weight:bold;font-size:3rem;text-align:center;margin-bottom:5rem;-webkit-user-select:none;-ms-user-select:none;user-select:none}.login form div{display:flex;flex-direction:column;margin-bottom:2rem}.login form div label{font-size:1.4rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;color:#515a5a}.login form div input{border:none;outline:none;border-bottom:2px solid #515a5a;padding:.8rem 0;transition:all .4s ease-out}.login form div input:focus,.login form div input:valid{border-bottom:2px solid #28b463}.login form p{color:#515a5a;font-size:1.4rem;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;margin-bottom:5rem;text-align:end;transition:all .4s ease-out}.login form p:hover{color:#28b463}.login form button{outline:none;border:none;padding:1rem 0;background-color:#2ecc71;color:#fff;font-size:1.4rem;cursor:pointer;border-radius:.2rem;transition:all .4s ease-out}.login form button:hover{background-color:#28b463}
.profile{height:100%;display:flex;flex-direction:column}.profile h1{text-align:center;font-weight:bold;padding:1.5rem 0;font-size:2rem}.profile ul{display:flex;flex-direction:column;flex-grow:1;padding:2rem}.profile ul li{margin-bottom:2rem}.profile ul li h2{font-weight:bold;font-size:1.8rem}.profile ul li p{font-size:2rem;color:#424949}.profile ul li:first-of-type p{color:red}
.edit_profile{height:100%;display:flex;flex-direction:column}.edit_profile h1{text-align:center;font-weight:bold;padding:1.5rem 0;font-size:2rem;border-bottom:1px solid #000}.edit_profile form{flex-grow:1;display:flex;flex-direction:column;justify-content:space-evenly;padding:0 2rem}.edit_profile form div{display:flex;flex-direction:column}.edit_profile form div label{font-size:2rem}.edit_profile form div input{margin-top:.5rem;font-size:2rem;border-radius:.3rem}.edit_profile form div select{margin-top:.5rem;font-size:2rem;border-radius:.3rem}.edit_profile form button{font-size:2rem;cursor:pointer;border-radius:.3rem}
.class_list{height:100%;display:flex;flex-direction:column;align-items:center}.class_list h1{text-align:center;font-weight:bold;padding:1.5rem 0;font-size:2rem}.class_list table{border:1px solid #000;border-collapse:collapse;width:100%;text-align:center}.class_list table th{border:1px solid #000;padding:.8rem;font-weight:bold;font-size:1.4rem}.class_list table td{border:1px solid #000;padding:.8rem;font-size:1.4rem}.class_list table td button{padding:0 1rem;cursor:pointer}
.class_info{height:100%;display:flex;flex-direction:column;align-items:center}.class_info h1{text-align:center;font-weight:bold;padding:1.5rem 0;font-size:2rem}.class_info table{border:1px solid #000;border-collapse:collapse;width:100%;text-align:center}.class_info table th{border:1px solid #000;padding:.8rem;font-weight:bold;font-size:1.4rem}.class_info table td{border:1px solid #000;padding:.8rem;font-size:1.4rem}.class_info table td button{padding:0 1rem;cursor:pointer}.edit_form{display:flex;flex-direction:row;padding-top:2rem}.edit_form div input{padding:.5rem 0;text-align:center;height:100%}.edit_form div select{padding:.5rem 1rem;text-align:center;height:100%}.edit_form div button{padding:.5rem 1rem;text-align:center;height:100%}
.teacher{width:100vw;height:100vh;display:flex;flex-direction:column}.container{flex-grow:1}.btn{display:flex}.btn button{outline:none;border:none;background:none;border:1px solid #000;border-right:none;flex-grow:1;padding:1rem 0;cursor:pointer}
*,*:before,*:after{margin:0;padding:0;box-sizing:border-box;-webkit-box-sizing:border-box}html{font-size:62.5%}body{font-family:"Poppins",sans-serif;line-height:1.4;font-weight:400}
